import React, { useState } from "react";
import Navbar from "../../Navbar/Navbar";
import AddDogs from "../../AddDogs/AddDogs";
import AllDogsList from "../../DogList/AllDogsList";
import { useSelector, useDispatch } from "react-redux";
import { login } from "../../../store/actions/loginAction";
import MuiAccordion from "@mui/material/Accordion";
import MuiAccordionSummary from "@mui/material/AccordionSummary";
import MuiAccordionDetails from "@mui/material/AccordionSummary";
import "./Admin.css";

const Admin = () => {
  const dispatch = useDispatch();
  const loggedInUser = useSelector((state) => state.inLoggedUser);
  const [user, setUser] = useState({
    username: "",
    password: "",
  });

  const updateState = (event) => {
    setUser({
      ...user,
      [event.target.name]: event.target.value,
    });
  };

  const submitUser = () => {
    dispatch(login(user.username, user.password));
  };

  const background = require("../../../images/background-form.png");

  window.scrollTo(0, 0);

  const rodica = require("../../../images/hundarutanhem/rodica.jpeg");
  const meggie = require("../../../images/hundarutanhem/meggie.jpeg");
  const molle = require("../../../images/hundarutanhem/molle.jpeg");
  const mario = require("../../../images/hundarutanhem/mario.jpeg");
  const svante = require("../../../images/hundarutanhem/svante.jpeg");
  const leggy = require("../../../images/hundarutanhem/leggy.png");

  return (
    <>
      {!loggedInUser.isLoggedIn ? (
        <>
          <Navbar black />
          <div
            className="contentWrapper centerWrapper loginWrapper"
            style={{ backgroundImage: `url(${background.default})` }}
          >
            <input
              className="loginInput"
              onChange={updateState}
              name="username"
              type="text"
              placeholder="Användarnamn"
            />
            <input
              className="loginInput"
              onChange={updateState}
              name="password"
              type="password"
              placeholder="Lösenord"
            />
            <button className="button" onClick={submitUser}>
              Logga in
            </button>
          </div>
        </>
      ) : (
        <>
          <Navbar />
          <div className="heroNavy">
            <h2 style={{ margin: "0" }}>Admin</h2>
          </div>
          <div className="contentWrapper">
            <MuiAccordion>
              <MuiAccordionSummary
                expandIcon={<i className="fas fa-angle-down fa-lg"></i>}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <h3>Publicera en hund</h3>
              </MuiAccordionSummary>
              <MuiAccordionDetails>
                <AddDogs />
              </MuiAccordionDetails>
            </MuiAccordion>
            <MuiAccordion>
              <MuiAccordionSummary
                expandIcon={<i className="fas fa-angle-down fa-lg"></i>}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <h3>Bilder</h3>
              </MuiAccordionSummary>
              <MuiAccordionDetails>
                <div className="dogImageContainer">
                  <h5>rodica</h5>
                  <div className="dogImageWrapper">
                    <img
                      src={rodica.default}
                      alt="rodica"
                      className="dogImage"
                    />
                  </div>
                </div>
                <div className="dogImageContainer">
                  <h5>meggie</h5>
                  <div className="dogImageWrapper">
                    <img
                      src={meggie.default}
                      alt="meggie"
                      className="dogImage"
                    />
                  </div>
                </div>
                <div className="dogImageContainer">
                  <h5>molle</h5>
                  <div className="dogImageWrapper">
                    <img src={molle.default} alt="molle" className="dogImage" />
                  </div>
                </div>
                <div className="dogImageContainer">
                  <h5>mario</h5>
                  <div className="dogImageWrapper">
                    <img src={mario.default} alt="mario" className="dogImage" />
                  </div>
                </div>
                <div className="dogImageContainer">
                  <h5>svante</h5>
                  <div className="dogImageWrapper">
                    <img
                      src={svante.default}
                      alt="svante"
                      className="dogImage"
                    />
                  </div>
                </div>
                <div className="dogImageContainer">
                  <h5>leggy</h5>
                  <div className="dogImageWrapper">
                    <img src={leggy.default} alt="leggy" className="dogImage" />
                  </div>
                </div>
              </MuiAccordionDetails>
            </MuiAccordion>
            <MuiAccordion>
              <MuiAccordionSummary
                expandIcon={<i className="fas fa-angle-down fa-lg"></i>}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <h3>Hundar</h3>
              </MuiAccordionSummary>
              <MuiAccordionDetails>
                <AllDogsList />
              </MuiAccordionDetails>
            </MuiAccordion>
          </div>
        </>
      )}
    </>
  );
};

export default Admin;
