import React from "react";
import { useSelector } from "react-redux";
import WizardResultsRedux from "../../Wizard/WizardResultsRedux";
import Navbar from "../../Navbar/Navbar";
import './Adoptionsresultat.css';

const Adoptionsresultat = ({ state, data }) => {
  const fetchedDogs = useSelector((state) => state.filteredDogs);
  return (
    <>
      <Navbar black />
      <div className="resultContainer">
        {fetchedDogs.length ? 
        <>
        <h2 className="resultHeading">Du har blivit matchad!</h2>
        </>
        : 
        <>
        <h3 className="resultHeading">Just nu finns det tyvärr ingen hund som matchar med dig</h3>
        <p className="resultText">
          Men ge inte upp! Vi på Matching Paws får löpande in hundar
          som behöver omplaceras och snart kanske det kommer in en
          hund som passar just dig.
        </p>
        </>}
        <div id="dogResult" className="resultWrapper">
          <WizardResultsRedux fetchedDogs={fetchedDogs} />
        </div>
      </div>
    </>
  );
};

export default Adoptionsresultat;
