import React, { useState, useEffect } from "react";
import axios from "axios";
import "./DogPage.css";
import Navbar from "../Navbar/Navbar";
import {
  SkeletonImage,
  SkeletonText,
  SkeletonHeading,
} from "../Skeleton/SkeletonImage";
import LinearProgress from "@mui/material/LinearProgress";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { Event } from "../Tracking/index";

const DogPage = ({ match }) => {
  const [data, setData] = useState([{}]);
  const [percentageFysik, setPercentageFysik] = useState(0);
  const [percentageStorlek, setPercentageStorlek] = useState(0);
  const [percentageErfarenhet, setPercentageErfarenhet] = useState(0);
  const [seeEmail, setSeeEmail] = useState(false);

  let params = match.params;
  const id = params.id;

  const theme = createTheme({
    palette: {
      primary: {
        main: "#ea947e",
      },
      secondary: {
        main: "#f44336",
      },
    },
  });

  const showEmail = () => {
    setSeeEmail(!false);
  };

  const handleAdoptionLink = () => {
    Event(
      "ADOPTLINK",
      `Klickade på länk för ${data?.hundnamn}`,
      `Klickade på länk för ${data?.hundnamn}`
    );
  };

  useEffect(() => {
    const fetchDogs = async () => {
      const result = await axios(
        `https://matchingpaws-api.herokuapp.com/dogs/${id}`
      );
      setData(result.data);
    };
    fetchDogs();
  }, [id]);

  const handlePercentageFysik = () => {
    let percentageFysik = 0;

    if (data?.fysik === "låg") {
      percentageFysik = 10;
    }
    if (data?.fysik === "normal") {
      percentageFysik = 50;
    }
    if (data?.fysik === "hög") {
      percentageFysik = 100;
    }
    setPercentageFysik(percentageFysik);
  };

  const handlePercentageStorlek = () => {
    let percentageStorlek = 0;

    if (data?.storlek === "liten") {
      percentageStorlek = 10;
    }
    if (data?.storlek === "mellan") {
      percentageStorlek = 50;
    }
    if (data?.storlek === "stor") {
      percentageStorlek = 100;
    }
    setPercentageStorlek(percentageStorlek);
  };

  const handlePercentageErfarenhet = () => {
    let percentageErfarenhet = 0;

    if (data?.hunderfarenhet === "lite") {
      percentageErfarenhet = 10;
    }
    if (data?.hunderfarenhet === "ganska") {
      percentageErfarenhet = 50;
    }
    if (data?.hunderfarenhet === "mycket") {
      percentageErfarenhet = 100;
    }
    setPercentageErfarenhet(percentageErfarenhet);
  };

  useEffect(() => {
    if (!seeEmail) {
      window.scrollTo(0, 0);
    }
  });

  useEffect(() => {
    handlePercentageFysik();
    handlePercentageStorlek();
    handlePercentageErfarenhet();
  }, [data]);

  console.log("data?.allergi", data?.allergi);

  return (
    <>
      <Navbar black />
      {data?.bild ? (
        <div
          className="dogpage-image-wrapper"
          style={{ backgroundImage: `url(${data.bild})` }}
        ></div>
      ) : (
        <SkeletonImage />
      )}
      <div className="contentWrapper">
        {/* <Link to="/adoptera"><button className="buttonLink">Gå tillbaka</button></Link> */}
        {/* <button onClick={history.goBack}>Go back</button> */}
        <div className="dogpage-container">
          <div className="dogpage-info-wrapper">
            {data?.hundnamn ? <h2>{data?.hundnamn}</h2> : <SkeletonHeading />}
            <div>
              {data?.ras ? <p>Ras: {data?.ras}</p> : <SkeletonText />}
              {data?.alder ? <p>Ålder: {data?.alder} år</p> : <SkeletonText />}
            </div>
            <div className="descriptionWrapper">
              <p>
                <i>{data?.beskrivning}</i>
              </p>
            </div>
          </div>
        </div>
        <div className="circleProgressWrapper">
          <div className="circleWrapper">
            <h5>Krav på fysisk förmåga</h5>
            <div className="linearWrapper">
              <ThemeProvider theme={theme}>
                <LinearProgress
                  variant="determinate"
                  value={percentageFysik}
                  sx={{ width: "90%" }}
                  color="primary"
                />
              </ThemeProvider>
              <span className="linearWrapperText">{percentageFysik}%</span>
            </div>
          </div>
          <div className="circleWrapper">
            <h5>Krav på erfarenhet</h5>
            <div className="linearWrapper">
              <ThemeProvider theme={theme}>
                <LinearProgress
                  variant="determinate"
                  value={percentageErfarenhet}
                  sx={{ width: "90%" }}
                  color="primary"
                />
              </ThemeProvider>
              <span className="linearWrapperText">{percentageErfarenhet}%</span>
            </div>
          </div>
          <div className="circleWrapper">
            <h5>Mankstorlek</h5>
            <div className="linearWrapper">
              <ThemeProvider theme={theme}>
                <LinearProgress
                  variant="determinate"
                  value={percentageStorlek}
                  sx={{ width: "90%" }}
                  color="primary"
                />
              </ThemeProvider>
              <span className="linearWrapperText">{percentageStorlek}%</span>
            </div>
          </div>
        </div>
        <div>
          <div className="checkboxWrapper">
            {data?.allergi === "nej" ? (
              <span className="successIcon">
                <i className="fas fa-check"></i>
              </span>
            ) : (
              <span className="declineIcon">
                <i className="fas fa-times"></i>
              </span>
            )}
            <h5>Fäller päls</h5>
          </div>
          <div className="checkboxWrapper">
            {data?.katt === "ja" ? (
              <span className="successIcon">
                <i className="fas fa-check"></i>
              </span>
            ) : (
              <span className="declineIcon">
                <i className="fas fa-times"></i>
              </span>
            )}
            <h5>Kan bo med katt</h5>
          </div>
          <div className="checkboxWrapper">
            {data?.barn === "ja" ? (
              <span className="successIcon">
                <i className="fas fa-check"></i>
              </span>
            ) : (
              <span className="declineIcon">
                <i className="fas fa-times"></i>
              </span>
            )}
            <h5>Kan bo med småbarn</h5>
          </div>
          <div className="checkboxWrapper">
            {data?.hund === "tikar" ||
            data?.hund === "hanar" ||
            data?.hund === "båda" ? (
              <span className="successIcon">
                <i className="fas fa-check"></i>
              </span>
            ) : (
              <span className="declineIcon">
                <i className="fas fa-times"></i>
              </span>
            )}
            <h5>Kan bo med andra hundar</h5>
          </div>
        </div>
        <div className="contactWrapper">
          <h3>Kontakt</h3>
          <p>
            För att påbörja en kontakt med ägaren till {data?.hundnamn},
            kontakta <i>{data?.kontaktnamn}</i>.
          </p>
          {!seeEmail && data?.email && (
            <button className="buttonLinkSecret" onClick={showEmail}>
              Klicka här för mailadress
            </button>
          )}
          <div className="kravButtons">
            {seeEmail && data?.email && (
              <a
                className="buttonLink"
                href={`mailto:${data?.email}`}
                rel="noreferrer"
              >
                {data?.email}
              </a>
            )}
            {data?.lank && (
              <a href={data?.lank} target="_blank">
                <button
                  className="button buttonWrapper"
                  onClick={handleAdoptionLink}
                >
                  Länk till adoptionsannons
                </button>
              </a>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default DogPage;
