import React from "react";
import { Link } from "react-router-dom";
import { Event } from "../Tracking/index";
import './DogCard.css';

const DogCard = ({id, bild, hundnamn, ras, alder, lank}) => {
  const hundarutanhem = require("../../images/hundarutanhem/hundarutanhem-circle.png");
  const hasHundarutanhemLink = lank.includes("hundarutanhem.se");

    return (
      <div className="cardWrapper">
        <Link to={`/adoptera/${id}`} onClick={() => Event("ADOPT", `Läser mer om ${hundnamn}`, `Läser mer om ${hundnamn}`)}>
          <div className="cardImage" style={{backgroundImage: `url(${bild})`}}>
          </div>
        </Link>
        <div className="cardContentWrapper">
            <div className="cardContentTop">
                <h2>{hundnamn}</h2>
                <h3>{alder} år</h3>
            </div>
            <p className="smallParagraph">{ras}</p>
            {hundarutanhem && hasHundarutanhemLink ? (
                <div className="cardSticker">Hundar Utan Hem</div>
                ) : <div className="cardStickerTransparent">Sticker</div>}
        </div>
      </div>
    )
  }

export default DogCard;
