import React from "react";
import { Link } from "react-router-dom";
import { Event } from "../Tracking/index";
import './DogBox.css';

const DogBox = ({id, bild, hundnamn, ras, alder, lank}) => {
  const hundarutanhem = require("../../images/hundarutanhem/hundarutanhem-circle.png");
  const hasHundarutanhemLink = lank.includes("hundarutanhem.se");

    return (
      <div className="dog-wrapper">
        <Link to={`/adoptera/${id}`} onClick={() => Event("ADOPT", `Läser mer om ${hundnamn}`, `Läser mer om ${hundnamn}`)}>
          <div className="dog-image-wrapper">
            <img alt="dog" src={bild} className="dog-image"/>
          </div>
        </Link>
        <div className="dog-info-wrapper">
            <h4>{hundnamn}</h4>
            <p>{ras}, {alder} år</p>
            {hundarutanhem && hasHundarutanhemLink && (
          <div className="dog-info-sticker">Hundar Utan Hem</div>
        )}
        </div>
      </div>
    )
  }

export default DogBox;
