import React, { useState, useEffect } from "react";
import { Link, NavLink } from "react-router-dom";
import "./NavBar.css";

const Navbar = ({ black }) => {
  const iconWhite = require("../../images/Icon-MatchingPaws-white.png");
  const iconBlack = require("../../images/Icon-MatchingPaws-black.png");
  const [navbar, setNavbar] = useState(false);

  const changeBackground = () => {
    if (window.scrollY >= 66) {
      setNavbar(true);
    } else {
      setNavbar(false);
    }
  };

  useEffect(() => {
    changeBackground();
    window.addEventListener("scroll", changeBackground);
  });

  return (
    <header className={navbar ? "navbar navbar-active" : "navbar"}>
      <Link to="/">
        <div className="navbar-logo">
          <img
            className="logo"
            alt="Logo"
            src={black ? iconBlack.default : iconWhite.default}
          />
        </div>
      </Link>
      <div id="hamburger">
        <input type="checkbox" />
        <span></span>
        <span></span>
        <span></span>
        <ul id="menu">
          <Link to="/">
            <li>
              <img className="logo" alt="Logo" src={iconBlack.default} />
            </li>
          </Link>
          <NavLink to="/adoptera" activeStyle={{ textDecoration: "underline" }}>
            <li>Hitta din match</li>
          </NavLink>
          <NavLink
            to="/omplacera"
            activeStyle={{ textDecoration: "underline" }}
          >
            <li>Omplacera</li>
          </NavLink>
          <NavLink
            to="/hundlivet"
            activeStyle={{ textDecoration: "underline" }}
          >
            <li>Livet med hund</li>
          </NavLink>
          <NavLink
            to="/adoptionsprocessen"
            activeStyle={{ textDecoration: "underline" }}
          >
            <li>Adoptionsprocessen</li>
          </NavLink>
          <NavLink
            to="/solskenshistorier"
            activeStyle={{ textDecoration: "underline" }}
          >
            <li>Solskenshistorier</li>
          </NavLink>
          <NavLink to="/om" activeStyle={{ textDecoration: "underline" }}>
            <li>Om oss</li>
          </NavLink>
        </ul>
      </div>
    </header>
  );
};

export default Navbar;
